<template>
  <div class="user" v-if="user">
    <div class="user__left">
        <img :src="user.avatar_url" alt="">
        <a :href="user.html_url" class="user__left-btn">ПОСЕТИТЬ</a>
    </div>
    <div class="user__right">
        <p>{{ user.login }}</p>
        <p>Репозиториев: <span>{{ user.public_repos }}</span></p>
        <p>Создан: <span>{{ user.created_at.slice(0, -10) }}</span></p>
        <p>Подписчиков: <span>{{ user.followers }}</span></p>
        <p>Подписок: <span>{{ user.following }}</span></p>
    </div>
  </div>
  <div class="sort" v-show="user">
    <h2 class="sort__title">Сортировка</h2>
    <div class="sort__box">
        <button 
            class="sort__box-btn" 
            v-for="(btn, index) in btns"
            :key="btn.name"
            :class="{active: isActive == index}"
            @click="sortBtn(index, btn.sortType)"
        >
            {{ btn.name }}
        </button>
    </div>
  </div>
</template>

<script>

import { mapState, mapMutations } from 'vuex'

export default {
    computed: {
        ...mapState(['user'])
    },

    data() {
        return {
            btns: [
                { name: 'Имя', sortType: 'name' },
                { name: 'Звезды', sortType: 'stargazers_count' },
                { name: 'Дата', sortType: 'created_at' }
            ],
            isActive: 0
        }
    },

    methods: {
        sortBtn(index, sortType) {
            this.isActive = index
            this.sort(sortType)
        },
        ...mapMutations(['sort'])
    }
}

</script>

