import { createStore } from 'vuex'
import axios from 'axios'

const store = createStore({
    state: {
        user: null,
        repositories: null,
        error: null,
        url: 'https://api.github.com/users/',
        currentSort: 'name'
    },
    mutations: {
        userInfo(state, payload) {
            state.user = payload
            state.error = null
        },

        resetUser(state) {
            state.user = null
            state.error = null
            state.repositories = null
        },

        getError(state, error) {
            if(error.response.status === 403) {
                state.error = 'Вас забанили на данном сайте определенное время...'
            } else {
                state.error = 'Пользователь не найден'
            }
        },

        reposInfo(state, payload) {
            state.repositories = payload
        },

        sort(state, payload) {
            state.currentSort = payload
        }
    },
    actions: {
        async userInfo({commit, state}, search) {
            try {
                const res = await axios.get(`${state.url}${search}`)
                const repos = await axios.get(`${state.url}${search}/repos`)
                commit('reposInfo', repos.data)
                commit('userInfo', res.data)
            } catch (error) {
                commit('getError', error)
                console.log('Произошла ошибка при получении данных');
            }
        }
    },
    getters: {
        getReposSort(state) {
            if(state.repositories) {
                return state.repositories.sort((prev, next) => {
                    let mod = 1
                    if(prev[state.currentSort] > next[state.currentSort]) {
                        return -1 * mod
                    }
                })
            }
        }
    }
})


export default store